	module.exports = {"data":{"markdownRemark":{"html":"<h2>Text Decoration Property</h2>\n<p>The text-decoration property specifies the decoration added to text. It has 6 possible values: </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token selector\">.none</span> <span class=\"token punctuation\">{</span><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> none<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.underline</span> <span class=\"token punctuation\">{</span><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> underline<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.line-through</span> <span class=\"token punctuation\">{</span><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> line-through<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.overline</span> <span class=\"token punctuation\">{</span><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> overline<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.initial</span> <span class=\"token punctuation\">{</span><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> initial<span class=\"token punctuation\">}</span>\n    <span class=\"token selector\">.inherit</span> <span class=\"token punctuation\">{</span><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> inherit<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><strong>Note</strong>: In CSS3 you can use the text-decoration-color property to change the color of the decoration, otherwise the color is the same as the color of the text.</p>\n<h4>More Information:</h4>\n<p>Docs: <a href='https://developer.mozilla.org/en-US/docs/Web/CSS/text-decoration' target='_blank' rel='nofollow'>Mozilla Developer Network</a></p>\n<p>Browser Support: <a href='http://caniuse.com/#search=text-decoration' target='_blank' rel='nofollow'>Caniuse</a></p>","fields":{"slug":"/css/properties/text-decoration-property/"},"frontmatter":{"title":"Text Decoration Property"}}},"pathContext":{"slug":"/css/properties/text-decoration-property/"}}

